.TaskList {
    width: 600px;
  }
  
  .TaskListItem {
    display: flex;
    align-items: center;
    padding: 12px;
  
    box-shadow: 0px 2px 1px -1px rgba(0, 0, 0, 0.2),
      0px 1px 1px 0px rgba(0, 0, 0, 0.14), 0px 1px 3px 0px rgba(0, 0, 0, 0.12);
  }
  
  .TaskListItem + .TaskListItem {
    margin-top: 12px;
  }
  
   
  .TaskListText {
    flex: 0 0 65%;
    margin-top: 0;
    margin-bottom: 0;
    margin-right: 16px;
    border-radius: 3px;
  }
  
  .TaskListButton {
    margin-right: 4px;
    margin-left: 4px;
  }